.App {
  color: white;
  font-family: 'Montserrat', sans-serif;
}

.navbar-nav .dropdown-menu {
  float: none;
  background: transparent;
}

.dropdown-item {
  color: #a1aab3;
}

.intro {
  position: relative;
  text-align: center;
  top: 45vh;
  width: 100%;
  font-family: 'Michroma', sans-serif;
  text-shadow: 3px 3px 5px #000000;
}
.works {
  position: relative;
  text-align: center;
  top: 100vh;
  width: 100%;
  font-family: 'Michroma', sans-serif;
  text-shadow: 3px 3px 5px #000000;
}
.header {
  z-index: 2;
}

h5, .header {
  font-family: 'Michroma', sans-serif;
  color: rgb(211, 211, 211);
}

h5 {
  text-align: center;
}

#tsparticles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -2;
}

html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  background-image: linear-gradient(to bottom, #000000 0%, #080316 100%);
  height: 400rem;
  min-height: 600vh;
  display: flex;
}

body {
  background: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
}

#root {
  overflow: hidden;
  background: transparent;
}

.card1 {
  border-radius: 5px;
  background-size: cover;
  background-position: center ;
  background-repeat: no-repeat;
  will-change: transform;
  width: 90%;
  height: 90%;
  background-image: url(./Mateo.png);
  left: -5%;
}

.containerfade {
  position: relative;
  top: -20vh;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.description {
  text-align: justify;
  text-shadow: 1px 1px 2px black, 0 0 1em black, 0 0 0.2em black;
  display: flex;
  flex-direction: column;
  top: 160vh;
  position: relative;
  max-width: 65%;
  z-index: 1;
}

.Knot, .Voting, .Picas, .Jaguar, .Timers {
  text-align: center;
  text-shadow: 1px 1px 2px black, 0 0 1em black, 0 0 0.2em black;
  padding: 5%;
  position: relative;
}

.Knot {
  top: 100vh;
}

.Voting {
  top: 160vh;
}

.Picas {
  top: 220vh;
}

.Jaguar {
  top: 280vh;
}

.Timers {
  top: 340vh;
}

a {
  font-family: 'Michroma', sans-serif;
  color: rgb(196, 196, 196);
}

.refLink {
  text-align: center;
  font-size: 2em;
  padding-top: 5%;
}

a:hover {
  text-decoration: none;
  color: aliceblue;
}

.Knot p, .Voting p, .Picas p, .Jaguar p, .Timers p {
  padding: 10%;
  text-align: justify;
}
